.footer {
  max-width: 880px;
  margin: 0 auto;
  padding: 0 0 60px 0;
}

@media screen and (min-width: 768px) and (max-width: 1380px) {
  .footer {
    max-width: 730px;
  }
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .footer {
    max-width: 282px;
    margin-bottom: 0;
  }
}